<template>
<div class="about">
    <h1>{{id ?'编辑':'新建'}}管理员</h1>
    <el-form label-width="120px" @submit.native.prevent="save">
        <el-form-item  label='用户名'>
            <el-input v-model="model.username"></el-input>
        </el-form-item>
        <el-form-item  label='密码'>
            <el-input  v-model="model.password"></el-input>
        </el-form-item>
        <el-form-item>
            <el-button type='primary' native-type="submit">保存</el-button>
        </el-form-item>
    </el-form>
</div>
</template>
<script>
export default {
    props:{
        id:{}
    },
    data(){
        return{
            model:{},
            parens:{},
        }

    },
    methods:{
        async save(){
            let res 
            if(this.id){
               res = await this.$http.put(`rest/admin_users/dist/${this.id}`,this.model)
            }else{
               res = await this.$http.post('rest/admin_users/create',this.model)
            }
            this.$router.push('/admin_users/list')
            this.$message({
          message: '恭喜你，这是一条成功消息',
          type: 'success'
        }); 
        },
        async fetch(){
            const res = await this.$http.get(`rest/admin_users/dist/${this.id}`)
            this.model=res.data
        },
    },
    created(){
        this.id && this.fetch()
        
    }
}
</script>