<template>
<div class="about">
    <h1>{{id ?'编辑':'新建'}}分类</h1>
    <el-form label-width="120px" @submit.native.prevent="save">
        <el-form-item  label='上级名称'>
            <el-select v-model="model.parens">
            <el-option v-for="item in parens" :key="item._id" :label="item.name" :value="item._id"></el-option>
       </el-select>
        </el-form-item>
        <el-form-item  label='名称'>
            <el-input v-model="model.name"></el-input>
        </el-form-item>
        <el-form-item>
            <el-button type='primary' native-type="submit">保存</el-button>
        </el-form-item>
    </el-form>
</div>
</template>
<script>
export default {
    props:{
        id:{}
    },
    data(){
        return{
            model:{},
            parens:{},
        }

    },
    methods:{
        async save(){
            let res 
            if(this.id){
               res = await this.$http.put(`rest/categories/dist/${this.id}`,this.model)
            }else{
               res = await this.$http.post('rest/categories/create',this.model)
            }
            this.$router.push('/list')
            this.$message({
          message: '恭喜你，这是一条成功消息',
          type: 'success'
        }); 
        },
        async fetch(){
            const res = await this.$http.get(`rest/categories/dist/${this.id}`)
            this.model=res.data
        },
        async fetchparenoptions(){
            const res = await this.$http.get('rest/categories/list')
            this.parens=res.data
        },
    },
    created(){
        this.fetchparenoptions()
        this.id && this.fetch()
        
    }
}
</script>