<template>
<div class="about">
    <el-table :data='items'  >
        <el-table-column  prop="_id" label='id' width='300'></el-table-column>
        <el-table-column   prop="username" label='用户名' width='300'></el-table-column>
        <el-table-column 
        
      label="操作"
      width="200">
      <template  slot-scope="scope">
        <el-button @click="$router.push(`edit/${scope.row._id}`)" type="text" size="small">编辑</el-button>
        <el-button type="text" @click="remove(scope.row)" size="small">删除</el-button>
      </template>
    </el-table-column>
    </el-table>
    
</div>
</template>
<script>
export default {
    data(){
        return{
            items:[]
        }
    },
    methods:{
        async fetch(){
            const res = await this.$http.get('rest/admin_users/list')
            this.items=res.data
        },
        async remove(row){
            this.$confirm(`此操作将永久删除${row.name}, 是否继续?`, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(async () => {
            const res = await this.$http.delete(`rest/admin_users/dist/${row._id}`)
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
          this.fetch()
        })

        }
    },
     created(){
         this.fetch()
         

}
}

</script>