import Vue from 'vue'
import Router from 'vue-router'
import about from './views/About.vue'
import login from './views/login.vue'
import create from './views/create.vue'
import list from './views/list.vue'
import itemcreate from './views/itemcreate.vue'
import itemlist from './views/itemlist.vue'
import herocreate from './views/herocreate.vue'
import herolist from './views/herolist.vue'
import articlescreate from './views/articlescreate.vue'
import articleslist from './views/articleslist.vue'
import abscreate from './views/abscreate.vue'
import abslist from './views/abslist.vue'
import adminusercreate from './views/adminusercreate.vue'
import adminuserlist from './views/adminuserlist.vue'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path:'/login',
      name:'login',
      component:login,
      meta:{ isPublic :true}
  
    },
    {
      path: '/',
      name: 'about',
      component: about,
      children:[
        {path:'/create',component:create},
        {path:'/edit/:id',component:create,props:true},
        {path:'/list',component:list},

        {path:'items/create',component:itemcreate},
        {path:'items/edit/:id',component:itemcreate,props:true},
        {path:'items/list',component:itemlist},

        {path:'heroes/create',component:herocreate},
        {path:'heroes/edit/:id',component:herocreate,props:true},
        {path:'heroes/list',component:herolist},

        {path:'articles/create',component:articlescreate},
        {path:'articles/edit/:id',component:articlescreate,props:true},
        {path:'articles/list',component:articleslist},

        {path:'abs/create',component:abscreate},
        {path:'abs/edit/:id',component:abscreate,props:true},
        {path:'abs/list',component:abslist},

        {path:'admin_users/create',component:adminusercreate},
        {path:'admin_users/edit/:id',component:adminusercreate,props:true},
        {path:'admin_users/list',component:adminuserlist}
      ]
    },
  ]
})
router.beforeEach((to, from, next) => {
  if(!to.meta.isPublic&&!localStorage.token){
    return next('/login')
  }
  next()
})
export default router
